/* âœ… Conteneur principal du tableau de bord SuperAdmin */
.superadmin-dashboard {
    max-width: 1200px;
    margin: auto;
    padding: 20px;
    text-align: center;
}

/* âœ… Style du titre principal */
.dashboard-title {
    font-size: 28px;
    color: #333;
    margin-bottom: 10px;
}

/* âœ… Style du message d'accueil */
.dashboard-message {
    font-size: 18px;
    color: #666;
}

/* âœ… Conteneur des boutons d'action */
.admin-buttons {
    display: flex;
    justify-content: center;
    gap: 15px;
    margin-bottom: 20px;
}

/* âœ… Style gÃ©nÃ©ral des boutons */
.admin-buttons button {
    cursor: pointer;
    border: none;
    border-radius: 5px;
    font-weight: bold;
    transition: all 0.2s ease-in-out;
}

/* âœ… DÃ©finition des tailles dynamiques */
.btn-small {
    padding: 6px 12px;
    font-size: 14px;
}

.btn-medium {
    padding: 10px 20px;
    font-size: 16px;
}

.btn-large {
    padding: 14px 28px;
    font-size: 18px;
}

/* âœ… Style des boutons avec couleurs spÃ©cifiques */
.btn-add-user {
    background-color: #ffc107;
    color: black;
}

.btn-dashboard-user {
    background-color: #007bff;
    color: black;
}

/* âœ… Effets interactifs */
.admin-buttons button:hover {
    opacity: 0.8;
}

.admin-buttons button:active {
    transform: scale(0.95);
}

/* âœ… Assure que les boutons restent au premier plan */
button {
    position: relative;
    z-index: 10;
}